@import url(https://fonts.googleapis.com/css?family=Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app {
  min-width: 100vw;
  min-height: 100vh;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;

  background-size: cover;
  background-position: bottom;
  background-color: #4459fe;
  text-align: center;
  overflow: hidden;
  -webkit-transition: 0.4 ease-out;
  transition: 0.4 ease-out;
}

#new-todo-submitbtn {
  width: 50px;
  height: 50px;
  border-radius: 25px;
  background: #ffb3b3;
  border-color: #ffdcf7;
  outline: none;
  right: 1%;
}

#new-todo-submitbtn i {
  font-size: 2em;
  position: relative;
  top: 2px;
  color: white;
}
.weather-card {
  margin: 20px;
  width: 360px;
  height: 640px;
  /*background-image: url('https://png.pngtree.com/thumb_back/fw800/background/20190221/ourmid/pngtree-abstract-modern-simple-cartoon-image_27699.jpg');*/
  background-image: url('https://techbeasts.com/wp-content/uploads/2018/09/Vivo-X23-Sock-Wallpaper_TechBeasts-1.png');
  background-size: cover;
  position: relative;
  border-radius: 26px;
  overflow: hidden;
  box-shadow: 0px 10px 15px 0px rgba(0, 0, 0, 0.4);
}

@media only screen and (max-width: 400px) {
  .weather-card {
    /*Full Screen - no margin. Only 1 card allowed*/
    margin: 0px;
    width: 100vh;
    height: 100vh;

    border-radius: 0px;
  }
}
.search-box {
  position: absolute;
  width: 100%;
  height: 60px;
  bottom: 0;

}

.search-bar {
  width: 100%;
  height: 100%;
  position: relative;
  border-radius: 20px 20px 0px 0px;
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 0 0 10px #202020cc;
  border: none;
  padding: 0 30px;
  outline: none;
  font-size: 16px;
  padding-right: 80px;

  color: #2855B0;
}

.search-bar::-webkit-input-placeholder {
  color: #0E124D;
}

.search-bar:-ms-input-placeholder {
  color: #0E124D;
}

.search-bar::-ms-input-placeholder {
  color: #0E124D;
}

.search-bar::placeholder {
  color: #0E124D;
}
.weather-details {
  position: relative;
  width: 100%;
  height: 25%;


  /* Var used for shadow/glow on bottom of box */
  --shadow-opacity: 0.4;

  --bg-color: 255, 114, 255;

  background-color: rgb(var(--bg-color));
  box-shadow: 0px 10px 15px 0px rgba(var(--bg-color), var(--shadow-opacity));
  box-shadow: 0px 15px 25px 0px rgba(166, 188, 178, 0.3);
  color: white;
  --corner-radius: 26px;
  border-radius: 0px 0px var(--corner-radius) var(--corner-radius);

  font-family: 'Rubik', sans-serif;
  font-size: 1.1rem;


  -webkit-transform: translateY(-170px);


          transform: translateY(-170px);
  -webkit-transition-duration: 1s;
          transition-duration: 1s;


}

.weather-details.active {
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
}

p {
  position: relative;
  padding-top: 10px;
}

p::after {
  position: absolute;
  content: '';
  width: 10%;
  height: 2px;
  bottom: -2px;
  left: 119px;
  background-color: white;
}

.temp-text {
  font-size: 4.0rem;
  font-weight: bold;
  margin-top: 20px;
  margin-bottom: -20px;
}

.location-text {
  position: absolute;
  right: 0;
  bottom: 10px;
  margin-right: 20px;
  font-size: 1.2rem;
  color: rgb(255, 255, 255);
}

.date-text {
  position: absolute;
  right: 0;
  bottom: -40px;
  margin-right: 20px;
  font-size: 1rem;
  color: rgb(255, 255, 255);
}

.weather-details.clear {

  --bg-color: 99, 163, 117;

  background-color: rgb(var(--bg-color));
  box-shadow: 0px 10px 15px 0px rgba(var(--bg-color), var(--shadow-opacity));
}

.weather-details.raining {

  --bg-color: 62, 91, 163;

  background-color: rgb(var(--bg-color));
  box-shadow: 0px 10px 15px 0px rgba(var(--bg-color), var(--shadow-opacity));
}

.weather-details.cloudy {

  --bg-color: 175, 150, 162;

  background-color: rgb(var(--bg-color));
  box-shadow: 0px 10px 15px 0px rgba(var(--bg-color), var(--shadow-opacity));
}

.weather-details.warm {
  --bg-color: 213, 122, 102;

  background-color: rgb(var(--bg-color));
  box-shadow: 0px 10px 15px 0px rgba(var(--bg-color), var(--shadow-opacity));
}

.weather-icon {

  --icon-size: 220px;

  width: var(--icon-size);
  height: var(--icon-size);
  position: absolute;
  top: 35%;
  left: 50%;
  -webkit-transform: translateX(-52%);
          transform: translateX(-52%);

  margin-left: 10px;

  background-image: url('https://cdn1.iconfinder.com/data/icons/weather-189/64/weather-icons-cloudy-256.png');
  background-size: cover;

  /* Icon is black so needs to be changed to be lighter  */
  -webkit-filter: contrast(200%) invert(100%);
          filter: contrast(200%) invert(100%);
  /* will make it white */

}


/* Icons */

.weather-icon.warm {
  background-image: url('https://cdn3.iconfinder.com/data/icons/weather-icons-8/512/weather-sunny-512.png');
  background-size: cover;
}

.weather-icon.cloudy {
  background-image: url('https://cdn3.iconfinder.com/data/icons/weather-609/64/weather_cloud_cloudy-256.png');
  background-size: cover;
}

.weather-icon.raining {
  background-image: url('https://cdn3.iconfinder.com/data/icons/streamline-icon-set-free-pack/48/Streamline-84-512.png');
  background-size: cover;
}

.weather-text {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  bottom: 100px;
  font-size: 2rem;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
}
